.gallery {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
}

.photo-card {
  min-width: 640px;
  margin-left: 46px;
  margin-top: 20px;
  /* width: calc((100% - var(--indent1) * (var(--items2) - 1)) / var(--items2)); */
}
.info {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 5px;
  text-shadow: 0px 1px 1px rgba(43, 41, 41, 0.14),
    0px 1px 3px rgba(0, 0, 0, 0.12), 0px 2px 1px rgba(0, 0, 0, 0.2);
}
.search-form {
  position: fixed;
  position: fixed;
  top: 0;
  width: 100%;
  height: 20px;
  display: flex;
  justify-content: center;
  padding-top: 15px;
  padding-bottom: 15px;
  /* margin-left: auto;
  margin-right: auto; */
  background: rgb(73, 73, 187);
}
.load-more {
  margin: auto;
  text-align: center;
  color: white;
  width: 120px;
  height: 30px;
  border-color: rgb(73, 73, 187);
  background-color: rgb(73, 73, 187);
  border-radius: 5px;
}
